// 基础卡效果
/* 
基础卡只附带最基础的伤害值，初始不带有任何属性和附加状态，和玩家其他状态会有反应
item -- 手牌内容
monster_name -- 怪物名称
 */
export function BasicsCard(item,monster_name){
    return {msg:`您对<span style="color:#c000ffd1;font-size:25px;font-family:Hywb"> ${monster_name} </span>造成<span style="color:red"> ${item.FDamageValue} </span>点${item.FTitle}伤害!`,value:item.FDamageValue}
}

export default {
    BasicsCard
}