import * as TweenMax from 'gsap/all'
import cardeffect from './cardeffect'

// 随机怪物卡生成
export async function Monster_Card_Generation(){
    var l = []
    for (let i = 1; i <= 30; i++) {
        l.push(i)
    }
}

//玩家开始出牌，对手牌进行判定
export async function judgment_start(id,obj,monster_name){
    var container = document.getElementsByClassName("curtainstyle_2")[0]
    var card = document.getElementById(`card${id}`)
    await TweenMax.gsap.to(`#card${id}`,0.2,{zIndex:44,left:(container.offsetWidth / 2 ) - (card.clientWidth / 2),top:"-230%",rotateY:"360deg"})
    return card_effect_judgment(obj,monster_name)
}

//手牌效果判定
export function card_effect_judgment(obj,monster_name){
    switch (obj.FType) {
        case "基础卡":
            return {msg:`您对<span style="color:#c000ffd1;font-size:25px;font-family:Hywb"> ${monster_name} </span>造成<span style="color:red"> ${obj.FDamageValue} </span>点${obj.FTitle}伤害!`,value:obj.FDamageValue}
            break;
        default:
            break;
    }
}

// 对当前打出的手牌进行效果判定
/* 
id -- 手牌svg的id
item -- 手牌所携带的内容/效果
monster_name -- 怪物名称 
*/
export async function judgment_start_current(item,monster_name){
    var container = document.getElementsByClassName("curtainstyle_2")[0]
    var card = document.getElementById(item.id)
    // await TweenMax.gsap.to(`#card${id}`,0.2,{zIndex:44,left:(container.offsetWidth / 2 ) - (card.clientWidth / 2),top:"-230%",rotateY:"360deg"})
    await TweenMax.gsap.to(`#${item.id}`,0.2,{zIndex:44,left:(container.offsetWidth / 2 ) - (card.clientWidth / 2),top:"-230%"})
    switch (item.FType) {
        case "基础卡":
            return cardeffect.BasicsCard(item,monster_name)
            break;
    
        default:
            break;
    }
}

export default {
    judgment_start_current
}